%centerRows {
  display: flex;
  justify-content: center;
  align-items: center;
}

%centerColumns {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

%center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
